import { Header } from "@/components/common";
import Dayjs from "dayjs";
import { USER_API } from "@/api/client";
import { mapState } from "vuex";
export default {
  name: "mBoxList",
  components: { Header },
  computed: {
    ...mapState(["local"]),
    locale() {
      return this.local.locale;
    },
    isBitKeep() {
      return this.local.UA.isBitKeep;
    }
  },
  // async asyncData(ctx) {},

  data() {
    return {
      isLoading: true,
      inviteNum: 0,
      helpNum: 0,
      listLoading: false,
      finished: false,
      refreshing: false,
      total: 20,
      start: 0,
      limit: 10,
      blindbox_list: []
    };
  },
  async created() {},
  async beforeMount() {
    this.isBitKeep &&
      BitKeepInvoke.setTitle(this.$t("ActivityBlindbox.title.myBlindboxText"));

    // this.onLoad(true);
  },
  async mounted() {
    this.showLoading();
    await this.$nextTick();
    this.hideLoading();
    this.isLoading = false;
  },

  methods: {
    async getList(pageNo = 1) {
      const { data, status } = await USER_API.mBoxList({
        start: (pageNo - 1) * this.limit,
        limit: this.limit
      });

      if (status == 1) {
        this.finished = true;
        return this.$toast.fail(data);
      }
      this.total = data.total;
      this.inviteNum = data.invite_number;
      this.helpNum = data.help_number;
      const list = data.list.map(item => {
        item.bg_icon = `url(${item.cover_image}) no-repeat  center center/cover`;
        return item;
      });
      if (!list || list.length == 0) {
        this.finished = true;
        return;
      }
      this.start = pageNo;
      if (pageNo == 1) {
        this.blindbox_list = list;
      } else {
        this.blindbox_list = [...this.blindbox_list, ...list];
      }
    },
    goFriendList(){
      this.$router.push({
        path: `/activity/blindbox/firendList`
      });
    },
    handlerBtn(item, type) {
      this.$router.push({
        path: `/activity/blindbox/detail/${item.id}`,
        query: {
          ownerIdentity: item.ownerIdentity,
        }
      });
    },
    showLoading() {
      this.Toast = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true
        // message: '请求中请稍后',
      });
      // const isBitKeepInvoke = typeof BitKeepInvoke !== "undefined";
      // // this.isLoading = true;
      // isBitKeepInvoke && BitKeepInvoke.showLoading();
    },
    hideLoading() {
      if (this.Toast) {
        this.Toast.clear();
      }
      // const isBitKeepInvoke = typeof BitKeepInvoke !== "undefined";
      // // this.isLoading = false;
      // isBitKeepInvoke && BitKeepInvoke.hideLoading();
    },
    back() {
      this.$router.back();
    },
    async onLoad(isRefresh) {
      if (this.refreshing) {
        this.blindbox_list = [];
        this.refreshing = false;
      }
      await this.getList(isRefresh ? 1 : this.start + 1);
      this.listLoading = false;
      if (this.total <= this.blindbox_list.length) {
        this.finished = true;
      }
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.listLoading = true;
      this.onLoad(true);
    }
  },

  filters: {
    date(val) {
      const date = Dayjs(val);
      const str =
        date.year() == Dayjs().year()
          ? "MM-DD HH:mm:ss"
          : "YYYY-MM-DD HH:mm:ss";
      return date.format(str);
    }
  }
};
